    --cor-principal: #2f89ff;
}


*{
    padding: 0;
    margin: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    background-color: #dae4e9;
    height: 100vh;
    width: 100vw;
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.container{
    background-color: white;
    height: 100%;
    width: 100%;
    display: flex;
    box-shadow: 5px 25px 30px rgb(172, 172, 172);
}
.cabecalho{
    display: none;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-position: center;
    background-size: 150%;
    border-radius: 20px 0 0 20px;
    width: 50%;
    height: 100%;
}

.principal{
    display: none;
    background-color: #2f89ff;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.form{
    background-color: white;
    border-radius: 15px;
    box-shadow: -5px 12px 20px #1f65c0, 5px 12px 20px #1f65c0;
    height: 372.2px;
    width: 270px;
    padding: 2rem .8rem;
    text-align: left;
}



.form .fas{
    display: block;
    text-align: center;
    color: #2980F2;
    font-size: 2.5rem;
}

.subtitle{
    text-align: center;
}

form label, form legend{
    font-size: .7rem;
    text-align: left;
    font-weight: 500;
}

form input[type='email'], form input[type='password'], form input[type='text'], form input[type='tel'], form select{
    display: block;
    width: 100%;
    height: 90%;
    background-color: #f7f7f7;
    border: 1px solid #c9c3c3;
    padding: .6rem;
    margin-bottom: 1rem;
    border-radius: 4px;
    font-size: .8rem;
}

form input[type='checkbox']{
    margin-right: .2rem;
}

label[for="manter-conectado"], .recupera-senha{
    margin-bottom: 1.8rem;
    color: rgb(110, 110, 110);   
    font-size: .7rem;
    font-weight: 500;
}

label[for="manter-conectado"]{
    float: left;
}

.recupera-senha{
    display: block;
    float: right;
}

.button-cadastro{
    border: 1px solid #2f89ff;
    border-radius: 3px;
    padding: .39rem .8rem;
    text-transform: uppercase;
    text-decoration: none;
    color:#2f89ff;
    font-size: .7rem;
    font-weight: 500;
    float: right;
    transition: all 0.5s ease-out;
}

.button-cadastro:hover{
    cursor: pointer;
    color: #095fcf;
    border-color: #095fcf;
}

.button-entrar{
    border: none;
    border-radius: 3px;
    padding: .55rem .8rem;
    background-color: #2f89ff;
    clear: left;
    float: left;
    color: white;
    text-transform: uppercase;
    font-size: .75rem;
    font-weight: 600;
    transition: all 0.5s ease-out;
}

.button-entrar:hover{
    cursor: pointer;
    background-color: #095fcf;
}

/*STYLE CADASTRO*/

.container-cadastro{
    background-color: white;
    height: 100%;
    width: 100%;
    display: flex;
    box-shadow: 5px 25px 30px rgb(172, 172, 172);
}

.form-cadastro .fas{
    display: block;
    text-align: center;
    color: #2980F2;
    font-size: 1.5rem;
}

.msg-sucess, .msg-error{
    display: block;
    padding: .19rem;
    font-family: 'Poppins', sans-serif;
    font-size: .7rem;
    border-radius: 4px;
    color: white;
}

.msg-error{
    background-color: #ff5656;
}
.msg-sucess{
    background-color: #5cb85c;
}


.form-cadastro{
    background-color: white;
    border-radius: 15px;
    box-shadow: -5px 12px 20px #1f65c0, 5px 12px 20px #1f65c0;
    height: 590.2px;
    width: 270px;
    padding: .5rem 1rem;
    text-align: left;
}

.cabecalho-cadastro{
    display: none;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-position: center;
    background-size: 110%;
    border-radius: 20px 0 0 20px;
    width: 50%;
    height: 100%;
}

form input[type='text']{
    width: 100%;
    height: 90%;
    background-color: #f7f7f7;
    border: 1px solid #c9c3c3;
    padding: .6rem;
    margin-bottom: 1rem;
    border-radius: 4px;
    font-size: .8rem;
}

.button-login{
    border: 1px solid #2f89ff;
    border-radius: 3px;
    padding: .39rem .8rem;
    text-transform: uppercase;
    text-decoration: none;
    color:#2f89ff;
    font-size: .7rem;
    font-weight: 500;
    float: right;
    transition: all 0.5s ease-out;
}
.button-login:hover{
    cursor: pointer;
    color: #095fcf;
    border-color: #095fcf;
}

.button-cadastrar{
    border: none;
    border-radius: 3px;
    padding: .55rem 1rem;
    background-color: #2f89ff;
    clear: left;
    float: left;
    color: white;
    text-transform: uppercase;
    font-size: .75rem;
    font-weight: 600;
    transition: all 0.5s ease-out;
}

.button-cadastrar:hover{
    cursor: pointer;
    background-color: #095fcf;
}

/*-------------------------------------------*/

.cabecalho-home{
    background-color: #2f89ff;
    padding: .3rem .2rem;
    min-height: 5%;
    width: 100%;
    align-self: flex-start;
}
.button-bar, .button-user{
    border: none;
    background-color: transparent;
    color: white;
    padding: 0 .2rem;
    font-size: 1.5rem;
    float: left;
    cursor: pointer;
}
.button-user{
    float: right;
}
.cabecalho-menu, .cabecalho-sair{
    clear: left;
    font-size: 1rem;
    width: auto;
    display: none;
    list-style-type: none;
    color: white;
    text-align: center;
    padding: .2rem .5rem;
    cursor: pointer;
    transition: color .5s;
}

.cabecalho-sair{
    display: block;
    float: right;
}

.cabecalho-menu li:hover, .cabecalho-sair li:hover, .button-bar:hover, .button-user:hover{
    color: #e6e3e3;
}


/**/
.main-home{
    background-color: white;
    width: 90%;
    height: 90%;
    box-shadow: 5px 25px 30px rgb(172, 172, 172);
    border-radius: 15px;
    padding: 1rem .8rem;
}

.main-table{
    display: block;
    width: 100%;
    max-height: 100%;
    overflow-y: auto;
    text-align: center;
    border-collapse: collapse;
    font-size: .3rem;
}
thead, tfoot{
    background-color: var(--cor-principal);
    color: white;
}

.td-nome, .td-telefone, .td-email{
    width: 90%;
}
.td-icons{
    width: 10%;
    padding: 0 2px;
}
/*-----------------------------------*/
.main-form{
    height: 100%;
    width: 100%;
    overflow-y: auto;
}

.main-form form input[type='tel'], .main-form form input[type='email']{
    max-width: 200px;
}

form select{
    max-width: 100px;
}
fieldset{
    padding: 0 .5rem;
    margin-bottom: .5rem;
    border-radius: 4px;
}

@media screen and (min-width: 800px) {
    .container{
        height: 80%;
        width: 85%;
        border-radius: 20px;
    }
    .principal{
        width: 50%;
        border-radius: 0 20px 20px 0;
    }
    .cabecalho{
        display: block;
    }

    .container-cadastro{
        height: 96%;
        width: 90%;
        border-radius: 20px;
    }

    .cabecalho-cadastro{
        display: block;
    }
    .button-bar{
        display: none;
    }
    .cabecalho-menu li{
        display: inline-block;
        width: 20%;
        cursor: pointer;
    }
    .cabecalho-menu{
        display: block;
    }
    .main-table{
        font-size: 1rem;
    }



}










